import {
    MAP_INIT,
} from '../constants/Map';

export function initMap(source,width,height) {

    
    return (dispatch) => {
        dispatch({
            type: MAP_INIT,
            payload: {
                source,
                width,
                height,
            }
        });

    }
}

export function changedMapSource(source) {

    return (dispatch) => {
        dispatch({
            type: MAP_INIT,
            payload: source
        });

    }
}
